@import url('./prod-res-panel.css');
@import url('./other-panel.css');

/* Size & position */
.panel-corp {
   bottom: 0;
   width: 100%;
   height: 10%;
}

/* Background */
.panel-corp img {
   position: absolute;
   bottom: 0;
   width: 100%;
}

/* Corporation Logo */
.corp-logo {
   position: absolute;
   width: calc(var(--default-size) * 3.4 * 1.5909);
   height: calc(var(--default-size) * 3.4);
   bottom: 6%;
   left: 6.7%;
   background: rgb(255, 255, 255);
   background: radial-gradient(
      circle,
      rgba(255, 255, 255, 0.5) 0%,
      rgba(255, 255, 255, 0) 85%,
      rgba(255, 255, 255, 0) 100%
   );
}
.corp-logo img {
   width: 100%;
}
